import React from 'react';
import NukaCarousel from 'nuka-carousel';

interface Props {
  children: React.ReactChild | React.ReactChild[];
}

function Carousel(props: Props) {
  return (
    <NukaCarousel
      autoplay
      heightMode='current'
      withoutControls
    >
      {props.children}
    </NukaCarousel>
  );
}

export default Carousel;