import React from 'react';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { makeStyles, Theme } from '@material-ui/core/styles';

import { CheckIcon } from '../../components/Svg';
import { FirstSlidePath, FirstSlideMobilePath } from '../../components/Images';
import Carousel from '../../components/Carousel';
import STRINGS from '../../strings';

const useStyles = makeStyles((theme: Theme) => {
  const slide = {
    color: theme.palette.common.white,
    minHeight:720,
    backgroundSize:'cover',
    backgroundPositionY:'50%',
    display:'flex',
    alignItems:'center',
    xs: {
      minHeight:600,
    }
  };

  const slideXS = {
    ...slide,
    minHeight:600
  }

  const textShadow = '0px 4px 10px rgba(0, 0, 0, 0.25)';

  return ({
    listItem: {
      display: 'flex',
      alignItems: 'center',
      marginBottom: 24,
    },
    listItemText: {
      fontWeight: theme.typography.fontWeightRegular,
      textShadow: textShadow,
    },
    title: {
      fontWeight: theme.typography.fontWeightBold,
      textShadow: textShadow,
    },
    subtitle: {
      fontWeight: theme.typography.fontWeightRegular,
      textShadow: textShadow,
      '& span': {
        fontWeight: theme.typography.fontWeightBold,
        color: theme.palette.secondary.main
      }
    },
    icon: {
      marginRight:16,
      textShadow: textShadow,
    },
    slide0: {
      ...slide,
      background: `url(${FirstSlidePath})`,
      [theme.breakpoints.down('xs')]: {
        ...slideXS,
        background: `url(${FirstSlideMobilePath})`
      }
    },
    slide1: {
      ...slide,
      background: `url(${FirstSlidePath})`,
      [theme.breakpoints.down('xs')]: {
        ...slideXS,
        background: `url(${FirstSlideMobilePath})`
      }
    },
  });
});

function Slider() {
  const classes = useStyles();

  function onButtonClick() {
    const target = document.querySelector('#products');

    if (target) {
      target.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }
  
  return (
    <Carousel>
      {STRINGS.HOME.CAROUSEL.SLIDES.map((slide, idx) => {
        const className = `slide${idx}` as keyof typeof classes;
        return (
          <div key={idx} className={classes[className]}>
            <Container>
              <Grid container justify='flex-end'>
                <Grid item xs={12} sm={6} md={5}>
                  <Typography classes={{root:classes.title}} color='inherit' variant='h2' paragraph>{slide.TITLE}</Typography>
                  <Typography classes={{root:classes.subtitle}} color='inherit' variant='h4' paragraph>{slide.SUBTITLE}</Typography>
                  { STRINGS.HOME.CAROUSEL.LIST.map((item, idx) => <SliderListItem key={idx} title={item.TITLE} />) }
                  <Button onClick={onButtonClick}  size='large' color='secondary' variant='contained'>{STRINGS.HOME.CAROUSEL.CTA}</Button>
                </Grid>
              </Grid>
            </Container>
          </div>
        );
      })}
    </Carousel>
  );
}

interface SliderListItemProps {
  title: string;
}

function SliderListItem(props: SliderListItemProps) {
  const classes = useStyles();
  return (
    <div className={classes.listItem}>
      <CheckIcon fontSize='large' classes={{root: classes.icon}} />
      <Typography classes={{root: classes.listItemText}} color='inherit' variant='h6'>{props.title}</Typography>
    </div>
  );
}

export default Slider;